import React, { useContext } from 'react';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import { UserContext } from '../../App';
import './Booking.css';

const Booking = () => {
    const { register, handleSubmit, watch, errors } = useForm();
    const [loggedInUser, setLoggedInUser] = useContext(UserContext);
   
    const onSubmit = data => {
        console.log('form submitted',data)
    };

    console.log(watch("example")); // watch input value by passing the name of it

    return (
        
        < form className="ship-form" onSubmit={handleSubmit(onSubmit)} >
          
            < input name="name" defaultValue={loggedInUser.name} ref={register({ required: true })} placeholder="Your Name" />
            { errors.name && <span className="error">Name is field is required</span>}

            < input name="email" defaultValue={loggedInUser.email} ref={register({ required: true })} placeholder="Your Email" />
            { errors.email && <span className="error">Email is field is required</span>}

            < input name="address" ref={register({ required: true })} placeholder="Your Address" />
            { errors.address && <span className="error">Address is field is required</span>}

            < input name="phone" ref={register({ required: true })} placeholder="Your Phone Number" />
            { errors.phone && <span className="error">Phone is field is required</span>}


            <input type="submit" />
        </form >
       
    );
};

export default Booking;